import React from 'react'
import { Link } from 'react-router-dom'
import './Postage.scss'
import $ from 'jquery'
// import User from './../Components/User/User'

// 引用图片 
import PosHeader_Tit1 from './../HomePage/images/HPHeader_Tit1.svg'
import HPUserInfo_Tit from './../HomePage/images/HPUserInfo_Tit.png'
import unLogin from './../HomePage/images/UnLogin.png'
import HPUserInfo_Bck1 from './../HomePage/images/HPUserInfo_Bck1.png'
import Pay_Price from './images/Pay_Price.png'
import BotLogo from './../HomePage/images/BotLogo.png'
// 引用组件
import Pay from './../Pay/Pay'

// 引用接口
import {receiveGiftOrder , checkReceive} from './../apis/api'



class Postage extends React.Component{
    constructor(props){
        super(props)
        this.state={
            userName : " ",
            userImg : ''
        }
        this.openPay = this.openPay.bind(this)
        this.bckAct = this.bckAct.bind(this)
    }
    componentDidMount(){
        // console.log(this.props)
            var Storage = window.localStorage
            // var mid = Storage.mid
            var nickname = Storage.nickname,
                avatar = Storage.avatar,
                _this = this;
            this.setState({
                userName : nickname,
                avatar : avatar
            })
            // this.refs.userImg.style.background = "url(" + Storage.avatar + ")"
            // this.refs.userImg.style.backgroundSize = "100% 100%"

            // this.checkRec = $.get(checkReceive , {mid:mid},function(res){
            //     if(res.data){
            //         // _this.props.history.push('/')
            //         // console.log(_this.props)
            //     }
            //     else{
            //         console.log('meiyoulingquguo');
            //     }
            // })
        

    }
    openPay(){
        // console.log(this.refs.childRef.refs.Pay);
        this.refs.childRef.refs.Pay.style.display = 'block'
    }
    bckAct(){
        this.props.history.push('/')
    }
    render(){
        return(
            <div className="Pos_Container">
                <div className="Pos_Main">

                    {/* 头部标题 */}
                    <header className="PosHeader_Container">
                        <div className="PosHeader_HeaderTit1">
                            <span className="PosHeader_HeaderTit1Text">
                                "冬季送暖"艾弗森亲授权
                            </span>
                        </div>

                        <div className="PosHeader_HeaderTit2">
                            <img src={PosHeader_Tit1} width="100%"/>
                        </div>
                    </header>


                    {/* 主体部分 */}
                    <main className="PosUI_Container">

                        {/* 头像部分 */}
                        <header className="PosUI_TitWrap">
                            <img src={HPUserInfo_Tit} width="100%"/>
                            <div className="PosUI_AvaWrap">
                                <img src={this.state.avatar || unLogin}/>
                            </div>
                            <p className="PosUI_IdWrap">
                                <span>{this.state.userName}</span>
                            </p>
                        </header>

                        {/* 支付界面 */}
                        <main className="PosUI_MainWrap">

                            {/* 左边框 */}
                            <section className="PosUI_ItemL">
                                <img src={HPUserInfo_Bck1} />
                            </section>
                            {/* 中间内容 */}
                            <section className="PosUI_ItemM">
                                
                                <div className="PosUI_Price">
                                    <img src={Pay_Price}/>
                                    <p>运费：8元</p>
                                </div>


                                <div className="PosUI_BtnWrap">
                                    <div className="PosUI_Btn" onClick={this.openPay}>
                                        <span>立即支付</span>
                                    </div>
                                </div>

                            </section>
                            {/* 右边框 */}
                            <section className="PosUI_ItemR">
                                <img src={HPUserInfo_Bck1} />
                                
                            </section>

                        </main>

                        {/* 底部 */}
                        <footer className="PosUI_FooterWrap">
                            <div className="PosUI_FooterMain">
                                
                                <p className="PosUI_FooterBtnWrap" onClick={this.bckAct}>
                                    <p className="PosUI_FooterBtn" >
                                        <span>邀请好友助力</span>
                                    </p>
                                </p>

                            </div>
                        </footer>
                    </main>



                    {/* 底部标识 */}
                    <footer className="PosBotLogo_Container">
                        <img src={BotLogo} width="100%"/>
                    </footer>

                    <Pay ref="childRef"></Pay>
                </div>
            </div>
        )
    }
}
export default Postage 