import React from 'react'
import {Link} from 'react-router-dom'
import './Login.scss'
import $ from 'jquery'

// 引用图片
import LoginHeadImg from './images/LoginHeader.png'
import LoginBtnNo from './images/LoginBtnNo.png'
import LoginBtn from './images/LoginBtn.png'

// 引用组件
import Tips from './../Components/Tips/Tips'
import Loading from './../Components/Loading/Loading';

// 引用接口
import {verifyCode , mobileCode , isOrNoAdd} from './../apis/api.js'

// 引用方法
import {SAVELOCAL} from './../Methods/Methods'


class Login extends React.Component{

    constructor(props){
        super(props)
        this.state = {
            phoneNum : '',
            codeNum :'',
            TipsInfo : '电话号码不能为空!!',
            codeText : '获取验证码',
            color:'rgb(16, 156, 11)',
            timer : 60,
            liked : true,
            code:true,
            Login:true,
            isLogin : true
        }
    }

    componentDidMount(){
           
    }

    componentWillUnmount(){
        this.setState({
            liked : false
        })
        clearInterval(this.vix)
        // console.log('11');

        if(this.verify){
            this.verify.abort()
        }
        else if(this.checkRec){
            this.checkRec.abort()
        }
        else if(this.mobileCode){
            this.mobileCode.abort()
        }
    }

    // 按回车键登录
    enter(e){
        e.keyCode === 13 && this.handle()
    }
    handle(e){
        this.Login(e)
    }
    /***********/

    // 获取电话号码
    getPhone(e){
        var target = e.target
        target.value = target.value.replace(/\D/g,'')
        // console.log(target.value)
        this.setState({
            phoneNum : target.value
        })
        console.log(this.state.codeNum.length)
        if(target.value.length === 11 && this.state.codeNum.length === 4 ){
            this.setState({
                Login : false
            })
        }
        else{
            this.setState({
                Login : true
            })
        }

        if(target.value.length === 11){
            this.setState({
                code : false
            })
        }
        else{
            this.setState({
                code : true,
            })
        }
        
    }

    // 获取验证码
    getCodeNum(e){
        var target = e.target
        target.value = target.value.replace(/\D/g,'')
        console.log(this.state.phoneNum.length)
        console.log(target.value.length)
        this.setState({
            codeNum : target.value
        })
        if(this.state.phoneNum.length === 11 && target.value.length === 4){
            this.setState({
                Login : false
            })
        }
        else{
            this.setState({
                Login : true
            })
        }
    }
    

    // 登录按钮
    Login(e){
        this.setState({
            Login : true
        })
        var _this = this
        let pattern = /^(13[0-9])|(14[5,7,9])|(15[^4])|(18[0-9])|(17[0,1,3,5,6,7,8])\d{8}$/
        if(this.state.phoneNum.length == 0 || this.state.phoneNum.length < 11 || !(pattern).test(this.state.phoneNum)){
            this.refs.childRef.refs.Tips.style.top = '0%' 
            setTimeout(function(){
                _this.refs.childRef.refs.Tips.style.top = '-40%'
            },2000)
            this.setState({
                TipsInfo : '手机号格式不正确!',
                Login : false,
                color : 'red'
            })
        }
        else if(this.state.codeNum == 0 || this.state.codeNum.length < 4){
            this.refs.childRef.refs.Tips.style.top = '0%' 
            setTimeout(function(){
                _this.refs.childRef.refs.Tips.style.top = '-40%'
            },2000)
            this.setState({
                TipsInfo : '验证格式不正确!',
                Login : false
            })


        }
        else{
            this.refs.childRefTwo.refs.Loading.style.display = "block"
            console.log($("input").val())
            console.log($("#code").val())
            var phone = $("input").val()
            var code = $("#code").val()
            var storage = window.localStorage
            var ActPath = storage.ActPath
            var _this = this
            if(this.state.isLogin){
                this.verify = $.post(verifyCode, {mobile:phone ,code:code} , function(res){
                    console.log(res);
                    if(res.msg === 'SUCCESS'){
                        _this.refs.childRefTwo.refs.Loading.style.display = "none"
                        
                        console.log('success')
                        SAVELOCAL('avatar' , res.data.avatar)
                        SAVELOCAL('count' , res.data.count)
                        SAVELOCAL('is_receive' , res.data.is_receive)
                        SAVELOCAL('keys' , res.data.keys)
                        SAVELOCAL('mid' , res.data.mid)
                        SAVELOCAL('nickname' , res.data.nickname)
                        SAVELOCAL('token' , res.data.token)
                        SAVELOCAL('be_mid' , res.data.mid)
    
                        if(ActPath){
                            _this.props.history.push(ActPath)
                        }
                        else{
                            var This = _this
                            var mid = storage.mid
                            $.get(isOrNoAdd , {mid : mid},function(res){
                                console.log(res)
                                if(res.data){ //未填写
                                    This.props.history.push('/UserInfo')
                                }else{  //已填写
                                    This.props.history.push('/')
                                }
                            })
                        }
    
    
                        
                    }
                    else{
                        _this.refs.childRef.refs.Tips.style.top = '0'
                        setTimeout(function(){
                            _this.refs.childRef.refs.Tips.style.top = '-40%'
                        },2000)
    
                        _this.setState({
                            TipsInfo : '验证码不正确!',
                            color: 'red',
                            Login : false
                        })
                        _this.refs.childRefTwo.refs.Loading.style.display = "none"
                    }
                 })
            }
        }
    }

    // 返回空的点击事件
    UngetCode(e){
        return false
    }
    UnLogin(e){
        return false
    }

    // 获取验证码
    getCode(e){
        let _this = this
        let pattern = /^(13[0-9])|(14[5,7,9])|(15[^4])|(18[0-9])|(17[0,1,3,5,6,7,8])\d{8}$/
        if(this.state.phoneNum.length == 0 || this.state.phoneNum.length < 11 || !(pattern).test(this.state.phoneNum)){
            this.refs.childRef.refs.Tips.style.top = '0%' 
            setTimeout(function(){
                _this.refs.childRef.refs.Tips.style.top = '-40%'
            },2000)
            this.setState({
                color:'red',
                TipsInfo : '手机号格式不正确!'
            })
            e.preventDefault()

        }
        else if(this.state.liked){
            let timer = this.state.timer
            this.setState({
                liked : false,
                code : true
            })
            clearInterval(this.vix)
            this.vix = setInterval(()=>{
                // 没搞懂!
                this.setState({timer:(this.state.timer-1)},()=>{
                    if(this.state.timer < 0){
                        clearInterval(this.vix)
                        this.setState({
                            codeText : '重新获取验证码',
                            liked:true,
                            timer:60,
                            code : false
                        })
                    }
                })
            },1000)
            this.refs.childRef.refs.Tips.style.top = '0%' 
            setTimeout(function(){
                _this.refs.childRef.refs.Tips.style.top = '-40%'
            },2000)
            this.setState({
                color:"rgb(16, 156, 11)",
                TipsInfo : '验证码已发送!'
            })

            console.log($("input").val());
            let phoneNum = $("input").val()

            this.moblieCode = $.post(mobileCode , {mobile : phoneNum } ,function(res){
                console.log(res);
            })
        }
    }

    

    render(){
        return (
            <div className="LoginWrap">
            <div className="login">
            <Loading ref="childRefTwo"></Loading>
            <Tips 
                ref="childRef"
                info={this.state.TipsInfo}
                color ={this.state.color}
            ></Tips>

            {/* 头部部分 */}
            <header className="LoginHeaderWrap">
                <div className="LoginHeader">
                    <Link to="/">
                        <i className="iconfont titleImg icon-leftrow"></i>
                    </Link>
                    <span>登陆</span>
                    
                </div>
            </header>

            {/* 登录界面*/}
            <main className="LoginMainWrap">
                <div className="LoginMain">
                    <div className="LoginForm" >
                        <form action="#">

                            <div className="phone">

                                <input 
                                    type="text" 
                                    name="phone" 
                                    placeholder="手机号" 
                                    autoComplete="off"
                                    maxLength="11"
                                    onInput = {this.getPhone.bind(this)}
                                />

                                <span 
                                    className={this.state.code ? 'Ucode' : 'code'}
                                    onTouchEnd={this.state.code ? this.UngetCode.bind(this) : this.getCode.bind(this)}
                                    
                                >
                                    {this.state.liked ? this.state.codeText : this.state.timer}
                                </span>
                            </div>

                            <div className="code">
                                <input 
                                    type="text"
                                    id="code" 
                                    name="code" 
                                    placeholder="验证码" 
                                    autoComplete="off"
                                    maxLength="4" 
                                    style={{marginTop : `${20}px`}}
                                    onInput = {this.getCodeNum.bind(this)}
                                    onKeyDown = {this.enter.bind(this)}
                                    
                                />
                                
                            </div>
                            
                        </form>
                    </div>
                    <div className={ this.state.Login ? 'unLoginBtnWrap' : 'LoginBtnWrap' }>
                        <div className="LoginBtn" onClick={this.state.Login ? this.UnLogin.bind(this) : this.Login.bind(this)}>
                            <span>登陆</span>
                        </div>
                    </div>


                </div>
            </main>
            </div>
                
            </div>
        )
    }
}
export default Login