import React from 'react'
import './WhereOpen.scss'

class WhereOpen extends React.Component{
    constructor(props){
        super(props)
        this.state={
            isHidden : true
        }
    }
    componentWillMount(){
        this.WOpen()
    }
    WOpen(){
        var browser = {
            versions: function () {
                var u = navigator.userAgent, app = navigator.appVersion;
                return {         //移动终端浏览器版本信息
                    trident: u.indexOf('Trident') > -1, //IE内核
                    presto: u.indexOf('Presto') > -1, //opera内核
                    webKit: u.indexOf('AppleWebKit') > -1, //苹果、谷歌内核
                    gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1, //火狐内核
                    mobile: !!u.match(/AppleWebKit.*Mobile.*/), //是否为移动终端
                    ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios终端
                    android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, //android终端或uc浏览器
                    iPhone: u.indexOf('iPhone') > -1, //是否为iPhone或者QQHD浏览器
                    iPad: u.indexOf('iPad') > -1, //是否iPad
                    webApp: u.indexOf('Safari') == -1, //是否web应该程序，没有头部与底部
                    QQbrw: u.indexOf('MQQBrowser') > -1, // QQ浏览器
                };
            }(),
            language: (navigator.browserLanguage || navigator.language).toLowerCase()
          }
    
          if (browser.versions.mobile) {//判断是否是移动设备打开。browser代码在下面
            var ua = navigator.userAgent.toLowerCase();//获取判断用的对象
              if (ua.match(/MicroMessenger/i) == "micromessenger") {
                      //在微信中打开
                      console.log('在微信中打开')
                      this.setState({
                        isHidden : false
                    })
              }
              if(ua.match(/Alipay/i)=="alipay"){
                console.log('在支付宝打开')
                this.setState({
                    isHidden : false
                })
              }
              if (ua.match(/WeiBo/i) == "weibo") {
                      //在新浪微博客户端打开
                      console.log('在微博中打开')
                      this.setState({
                        isHidden : false
                    })
              }
            //   if (ua.match(/QQ/i) == "qq") {
            //           //在QQ空间打开
            //           console.log('在qq空间中打开')
            //           this.setState({
            //             isHidden : false
            //         })
            //   }
              
              if(ua.match(/QQ/i) == "qq"){
                  console.log('qq打开')
                  this.setState({
                    isHidden : false
                })
              }
              if (browser.versions.QQbrw) {
                console.log('在qq浏览器中打开')
              } 
              if (browser.versions.ios) {
                      //是否在IOS浏览器打开
                      console.log('在ios中打开')
                    //   this.setState({
                    //     isHidden : false
                    // })
              } 
              if(browser.versions.android){
                      //是否在安卓浏览器打开
                      console.log('在安卓中打开')
              }
            } 
            else {
                    //否则就是PC浏览器打开
                    console.log('在pc打开')
            }
    }
    render(){
        return(
            <div className="WOpen_Container" hidden={this.state.isHidden ? true : false}>
                <div className="WOpen_Wrap" >
                    <div className="WOpen_Main">
                        <div className="getOrder_Tit">
                            <span>打开失败</span>
                        </div>
                        <div className="getOrder_Main">
                            <span>请点击右上角的设置使用在浏览器打开或选择其他浏览器</span>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default WhereOpen