import React from 'react'
import {Link} from 'react-router-dom'
import './Pay.scss'
import $ from 'jquery'

// 图片引用
// import zhifubao from './images/zhifubao.png'
import Close from './images/Close.png'

// 接口引用
import {receiveGiftOrder , ActiveName , payZfb , payWx , checkReceive} from './../apis/api'

// 引用外部组件
import Loading from './../Components/Loading/Loading';
import Tips from './../Components/Tips/Tips';

class Pay extends React.Component{

    constructor(props){
        super(props)
        this.state={
            TipsInfo : " ",
            color : " "
        }
    }

    componentDidMount(){
    }

    componentWillUnmount(){
        if(this.receiveGiftOrder){
            this.receiveGiftOrder.abort()
            console.log('1');
        }
        else if(this.payZfb){
            this.payZfb.abort()
            console.log('2');
        }
        else if(this.receiveGiftOrderItem){
            this.receiveGiftOrderItem.abort()
        }
        else if(this.payWx){
            this.payWx.abort()
        }
        else if(this.checkRec){
            this.checkRec.abort()
        }
        this.refs.childRef.refs.Loading.style.display = "none"
    }

    zfbPay(e){
        e.preventDefault();
        this.refs.childRef.refs.Loading.style.display = "block"
        if(!window.localStorage){
            return false
        }
        else{
            var storage = window.localStorage
            var mid = storage.mid
            var This = this
            this.receiveGiftOrder = $.ajax({
                url: receiveGiftOrder,
                
                 data: {"mid" : mid, "name": ActiveName,"type" : 1},
                 type: "get",
                 dataType: 'json',
                 contentType: "application/x-www-form-urlencoded",
                 success: function (data) {
                    var _this = This

                    if(!data.error){
                        var id = data.data.id
                        This.payZfb = $.get(payZfb, {'id' : id},function(res){
                            _this.refs.childRef.refs.Loading.style.display = "block"
                            window.location.href = res
                        })
                    }
                    else{
                        var That = _this 
                        // alert('您已参加此次活动，请下次再来')
                        _this.refs.TipsRef.refs.Tips.style.top = '0%' 
                        setTimeout(function(){
                            That.refs.TipsRef.refs.Tips.style.top = '-40%'
                        },2000)
                        _this.setState({
                            TipsInfo : '您已参加此次活动，请下次再来',
                            color : 'red'
                        })
                        _this.refs.childRef.refs.Loading.style.display = "none"
                    }

                    
                 }
             });
        }
    }
    wxPay(e){
        e.preventDefault();
        this.refs.childRef.refs.Loading.style.display = "block"
        if(!window.localStorage){
            return false
        }
        else{
            var storage = window.localStorage
            var mid = storage.mid
            var This = this
            this.receiveGiftOrderItem = $.ajax({
                url: receiveGiftOrder,
                
                 data: {"mid" : mid, "name": ActiveName,"type" : 2},
                 type: "get",
                 dataType: 'json',
                 contentType: "application/x-www-form-urlencoded",
                 success: function (data) {
                    console.log(data)
                    var _this = This
                    

                    if(!data.error){
                        var id = data.data.id
                        This.payWx = $.get(payWx, {'id' : id},function(res){
                            _this.refs.childRef.refs.Loading.style.display = "block"
                            window.location.href = res
                        })
                    }
                    else{
                        _this.refs.childRef.refs.Loading.style.display = "none"
                        var That = _this 
                        // alert('您已参加此次活动，请下次再来')
                        _this.refs.TipsRef.refs.Tips.style.top = '0%' 
                        setTimeout(function(){
                            That.refs.TipsRef.refs.Tips.style.top = '-40%'
                        },2000)
                        _this.setState({
                            TipsInfo : '您已参加此次活动，请下次再来',
                            color : 'red'
                        })
                    }
                    
                 }
             });
        }

    }

    Close(){
        console.log(this.refs.Pay);
        this.refs.Pay.style.display = 'none';
        if(this.receiveGiftOrder){
            this.receiveGiftOrder.abort()
            console.log('1');
        }
        else if(this.payZfb){
            this.payZfb.abort()
            console.log('2');
        }
        else if(this.receiveGiftOrderItem){
            console.log(this.receiveGiftOrderItem);
            // this.receiveGiftOrderItem.abort()
        }
        else if(this.payWx){
            this.payWx.abort()
        }
    }
    render(){
        return(
            <div className="PayWrap" ref="Pay">
                <div className="PayMain">
                    <div className="Pay">

                        <h1>8元邮费</h1>
                        <Link to="/" onClick={this.zfbPay.bind(this)}>
                            <div className="zhifubao"></div>
                        </Link>
                        <Link to="/PaySuc" onClick={this.wxPay.bind(this)}>
                            <div className="weChat"></div>
                        </Link>
                        
                        
                    </div>
                    <div className="Close">
                        <img src={Close} width="100%"/>
                        <span className="CloBtn" onClick={this.Close.bind(this)}></span>
                    </div>
                </div>

                <Loading ref="childRef"></Loading>
                <Tips 
                    ref = "TipsRef"
                    info={this.state.TipsInfo}
                    color ={this.state.color}
                ></Tips>
                
            </div>
        )
    }
}
export default Pay