import React from 'react'
import {Link} from 'react-router-dom'

// 引用样式
import './ActSuc.scss'

// 引用图片
import ActSucTit from './images/ActSuc.svg'
import BotLogo from './../HomePage/images/BotLogo.png'
// 引用组件

class ActSuc extends React.Component{
    constructor(props){
        super(props)
        this.state={
            isOrder : false
        }
        this.bckHP = this.bckHP.bind(this) //回到首页
        this.CloseGetOrder = this.CloseGetOrder.bind(this)
        this.getOrder = this.getOrder.bind(this)
    }
    //回到首页
    bckHP(){
        this.props.history.push('/')
    }
    getOrder(){
        this.setState({
            isOrder : true
        })
    }
    CloseGetOrder(){
        this.setState({
            isOrder : false
        })
    
    }
    render(){
        return(
            <div className="ActSuc_Container">
                <div className="ActSuc_wrap">  
                    
                    {/* 标题 */}
                    <header className="ActSuc_TitContainer">
                        <div className="ActSuc_TitWrap">
                            <img src={ActSucTit} width="100%"/>
                        </div>
                    </header>

                    {/* 内容 */}
                    <main className="ActSuc_ConContainer">
                        <div className="ActSuc_ConMain">
                            <div className="ActSuc_Item1">
                                <div className="ActSuc_Item2">
                                    <div className="ActSuc_Item2Tit">
                                        <span>艾弗森长护臂一套</span>
                                    </div>
                                    <div className="ActSuc_Item2Img">
                                    
                                    </div>
                                </div>
                            </div>
                            <div className="ActSuc_Item3">
                                <div className="ActSuc_Item3ShareWrap">
                                    <div className="ActSuc_Item3Share">
                                        <span>立即分享</span>
                                    </div>
                                </div>

                                <div className="ActSuc_Item3Other">
                                    <div className="ActSuc_Item3getOrderWrap">
                                        <div className="ActSuc_Item3getOrder" onClick={this.getOrder}>
                                            <span>查看订单</span>
                                        </div>
                                    </div>
                                    
                                    <div className="ActSuc_Item3BckHPWrap">
                                        <div className="ActSuc_Item3BckHP" onClick={this.bckHP}>
                                            <span>回到首页</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </main>


                    {/* 底部标识 */}
                    <footer className="ActSuc_BottomLogo">
                        <img src={BotLogo} width="100%"/>
                    </footer>


                    {/* 查看订单遮罩层 */}
                    <div className="getOrderModel" hidden={this.state.isOrder ? false : true}></div>

                    {/* 查看订单弹窗 */}
                    <div className="getOrder_Container" hidden={this.state.isOrder ? false : true}>
                        <div className="getOrder_Wrap">
                            <div className="getOrder_Tit">
                                <span>查看订单</span>
                            </div>
                            <div className="getOrder_Main">
                                <span>请关注幕后TV公众号进行订单查询</span>
                            </div>

                            <div className="getOrder_Btn">
                                <div className="getOrder_OK" onClick={this.CloseGetOrder}>
                                    <span>确定</span>
                                </div>
                                <div className="getOrder_NO" onClick={this.CloseGetOrder}>
                                    <span>取消</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default ActSuc