


export function SAVELOCAL (name , value){
   if(!window.localStorage){
       return false
   }
   else{
       var stroage = window.localStorage
       stroage.setItem(name , value)
   }
}


export function GETLOCAL (name){
    if(!window.localStorage){
        return false
    }
    else{
        var storage=window.localStorage;
        storage.getItem(name)
    }
 }
 

 export function setCookie(name,value)
{
    var Days = 30;
    var exp = new Date();
    exp.setTime(exp.getTime() + Days*24*60*60*1000);
    document.cookie = name + "="+ escape (value) + ";expires=" + exp.toGMTString();
}

export function getCookie(name)
{
        var arr,reg=new RegExp("(^| )"+name+"=([^;]*)(;|$)");
        if(arr=document.cookie.match(reg))
        return unescape(arr[2]);
        else
        return null;
}














//二维码产生的页面
export function getBase64(qrcode,canvas1,imgurl){
        var canvas = canvas1;
        var ctx = canvas.getContext("2d");
        var clientWidth = getWidth()
        canvas.width = clientWidth*4+300
        canvas.height = clientWidth*765/375*3
        var base64 = ''
        var img = new Image()
        img.crossOrigin = "Anonymous"
        img.src = imgurl
        var w = 2*clientWidth
        var cimg = convertCanvasToImage(qrcode)
        img.onload = function(){
            ctx.drawImage(img,0,0,clientWidth*4+300,clientWidth*765/375*3);
            ctx.drawImage(cimg,clientWidth+150,clientWidth*765/250,clientWidth*2,clientWidth*2.1);
            base64 = canvas.toDataURL("image/jpeg");
           
                sessionStorage.setItem("baseData",base64)
            
           
        }
      

}


