import React from 'react'
import { Link } from 'react-router-dom'
import './UserInfo.scss'
import './AreaList.scss'
import $ from 'jquery'
import {areap} from './AreaData_min'

// 引用图片
import InfoHeader from './images/InfoHeader.png'
import InfoBtn from './images/InfoBtn.png'
import InfoBtnNo from './images/InfoBtnNo.png'

// 引用接口
import {addAddress} from './../apis/api.js'


// 引用外部组件
import Loading from './../Components/Loading/Loading';
import Tips from './../Components/Tips/Tips'

class UserInfo extends React.Component{
    constructor(props){
        super(props)
        this.state={
            phoneNum : "",
            userName : "",
            area: "",
            addressInfo : "",
            liked : true,
            pArea:[],
            cArea:[],
            aArea:[],
            userAdd: "",
            provinceIf:true,
            cityIf:false,
            areaIf:false,
            is_default:2,
            ifmobile:false
        }
        this.getUser = this.getUser.bind(this) //获取表单内容 --- 姓名
        this.getPhone = this.getPhone.bind(this) //获取表单内容 --- 手机号码
        this.getAddress = this.getAddress.bind(this) //获取表单内容 --- 详细地址
    }
    componentDidMount(){
        this.setState({
            pArea : areap
        })
        // console.log(!this.state.userAdd);
    }
    componentWillUnmount(){
        if(this.addAddress){
            this.addAddress.abort()
        }
    }
    openArea(){
        this.setState({
            ifshowArea:!this.state.ifshowArea
        })
    }
    getUserInfo(e){
        var Storage = window.localStorage
        var mid = Storage.mid
        var nickname = Storage.nickname
        var abc=$('.addlistArea_navbar li');
        var _this = this
        this.setState({
            liked : true
        })


        let pattern = /^(13[0-9])|(14[5,7,9])|(15[^4])|(18[0-9])|(17[0,1,3,5,6,7,8])\d{8}$/
        if(this.state.phoneNum.length == 0 || this.state.phoneNum.length < 11 || !(pattern).test(this.state.phoneNum)){
            this.refs.childRef_item1.refs.Tips.style.top = '0%' 
            setTimeout(function(){
                _this.refs.childRef_item1.refs.Tips.style.top = '-40%'
            },2000)
            this.setState({
                TipsInfo : '手机号格式不正确!',
                color:'red',
                liked : false
            })
        }
        else{
            this.refs.childRef.refs.Loading.style.display = "block"
            var user = $("input[name = user]").val()
            var phone = $("input[name = phone]").val()
            var province = abc.eq(0).text()
            var city = abc.eq(1).text()
            var area = abc.eq(2).text()
            var AddressInfo = $("input[name = AddressInfo]").val()
            
            this.addAddress = $.post( addAddress , 
                    {
                        mid : mid,
                        nickname : nickname,
                        mobile : phone,
                        province : province,
                        city : city,
                        area : area,
                        address : AddressInfo
                    },
                    function(res){
                        // console.log(res);
                        if(res.msg === 'SUCCESS'){
                            _this.refs.childRef.refs.Loading.style.display = "none"
                            console.log(res);
                            _this.props.history.push('/')
                            
                        }
                        else if(res.error === 50001){
                            console.log(res.msg);
                            _this.setState({
                                TipsInfo : '发生错误,请重试!',
                                color:'red',
                                liked : false
                            })
                        }
                    }
                )
        }   
    }
    getUser(e){
        var target = e.target
        console.log(target.value);
        this.setState({
            userName : target.value
        })
        if(!target.value == "" && this.state.phoneNum.length === 11 && !this.state.addressInfo == "" && !this.state.userAdd == ""){
            this.setState({
                liked : false
            })
        }
        else{
            this.setState({
                liked : true
            })
        }
    }
    getPhone(e){
        var target = e.target
        console.log(target.value);
        target.value = target.value.replace(/\D/g,'')
        this.setState({
            phoneNum : target.value
        })
        if(target.value.length === 11 && !this.state.userName == "" && !this.state.addressInfo == "" && !this.state.userAdd == ""){
            this.setState({
                liked : false
            })
        }
        else{
            this.setState({
                liked : true
            })
        }
    }
    getAddress(e){
        var target = e.target
        console.log(target.value);
        this.setState({
            addressInfo : target.value
        })
        if(!target.value == "" && this.state.phoneNum.length === 11 && !this.state.userName == "" && !this.state.userAdd == ""){
            this.setState({
                liked : false
            })
        }
        else{
            this.setState({
                liked : true
            })
        }
    }

    getNo(e){
        e.preventDefault()
        return false
    }
    close(){
        this.refs.Area.style.display = "none"
    }
    goCity(index,itemName,e){
        // console.log($('.addlistArea1'));
        $('.addlistArea1').children().css('color','black')
        console.log(e.nativeEvent.target);
        this.setState({
            cArea:this.state.pArea[index].city,
            provinceIf:false,
            cityIf:true,
            areaIf:false,
            pAreaIf:true,
            ifshowArea:true,
            itemNameP:itemName,
            itemNameC:'',
            itemNameA:''
        })
        var b=e.nativeEvent.target;
        var d = b.parentNode.childNodes;
        for(var i=0;i<d.length;i++){
            if($(d[i]).attr('data-num') == index){
                $(d[i]).css('color','#E53E3E')
            }else{
                $(d[i]).css('color','black')
            }
        }
        for(var i=0;i<3;i++){
            if($('.addlistArea_navbar').children().eq(i).attr('data-num')== 1){
                $('.addlistArea_navbar').children().eq(i).children().css('background','#E53E3E')
            }else{
                $('.addlistArea_navbar').children().eq(i).children().css('background','none')
            }
        }
    }
    goArea(index,itemName,e){
        $('.addlistArea2').children().css('color','black')
        this.setState({
            aArea:this.state.cArea[index].area,
            provinceIf:false,
            cityIf:false,
            areaIf:true,
            itemNameC:itemName,
            itemNameA:''
        })
        var b=e.nativeEvent.target;
        var d = b.parentNode.childNodes;
        for(var i=0;i<d.length;i++){
            if($(d[i]).attr('data-num') == index){
                $(d[i]).css('color','#E53E3E')
            }else{
                $(d[i]).css('color','black')
            }
        }
        for(var i=0;i<3;i++){
            if($('.addlistArea_navbar').children().eq(i).attr('data-num')== 2){
                $('.addlistArea_navbar').children().eq(i).children().css('background','#E53E3E')
            }else{
                $('.addlistArea_navbar').children().eq(i).children().css('background','none')
            }
        }
    }
    goControl(index,itemName,e){
        this.setState({
            itemNameA:itemName
        })
        var b=e.nativeEvent.target;
        var d = b.parentNode.childNodes;
        for(var i=0;i<d.length;i++){
            if($(d[i]).attr('data-num') == index){
                $(d[i]).css('color','#E53E3E')
            }else{
                $(d[i]).css('color','black')
            }
        }
    }
    changeArea(e){
        console.log($(e.target).attr('data-num'))
        if($(e.target).attr('data-num')==0){
            this.setState({
                provinceIf:true,
                cityIf:false,
                areaIf:false
            })
        }else if($(e.target).attr('data-num')==1){
            this.setState({
                provinceIf:false,
                cityIf:true,
                areaIf:false
            })
        }else{
            this.setState({
                provinceIf:false,
                cityIf:false,
                areaIf:true
            })
        }
        var b=e.nativeEvent.target;
        var d = b.parentNode.childNodes;
        for(var i=0;i<d.length;i++){
            if($(d[i]).attr('data-num') == $(b).attr('data-num')){
                $(d[i]).children().css('background','#E53E3E')
            }else{
                $(d[i]).children().css('background','none')
            }
        }
    }
    CloseArea(){
        this.refs.Area.style.transform = "translateY(0px)"
    }
    controlAdd(){
        var abc=$('.addlistArea_navbar li');
        var userAdd=abc.eq(0).text()+' '+abc.eq(1).text()+' '+abc.eq(2).text();
        // console.log(userAdd);
        if(abc.eq(0).text()!='省份'&&abc.eq(1).text()!='城市'&&abc.eq(2).text()!='区县'){
            this.setState({
                userAdd:userAdd,
                ifshowArea:false,
                controlArea:true
            })
            // console.log(userAdd);
        }

        if(!this.state.userName == "" && this.state.phoneNum.length === 11 && !this.state.addressInfo == "" ){
            this.setState({
                liked : false
            })
        }
        else{
            this.setState({
                liked : true
            })
        }

    }
    render(){
        return(
            <div className="UserInfoWrap">

            <div className="UserInfoMain">
            <Loading ref="childRef"></Loading>
            <Tips 
                ref="childRef_item1"
                info={this.state.TipsInfo}
                color ={this.state.color}
            ></Tips>

            {/* 头部部分 */}
            <header className="UIHeaderWrap">
                <div className="UIHeader">
                    <Link to="/">
                        <i className="iconfont titleImg icon-leftrow"></i>
                    </Link>
                    <span>收货信息</span>
                </div>
            </header>


            {/* 信息填写部分 */}
            <main className="UIMainWrap">
                <div className="UIMain">
                    <div className="InfoForm">
                        <form action="#" style={{'background' : 'rgb(38,6,25)'}}>
        
                            <div className="user">
                                <label htmlFor="user">姓名</label>
                                <input 
                                    type="text" 
                                    name="user"
                                    autoComplete="off" 
                                    placeholder="姓名"
                                    onInput = {this.getUser}
                                />
                            </div>
        
                            <div className="InfoPhone">
                                <label htmlFor="phone">手机</label>
                                <input 
                                    type="text" 
                                    name="phone" 
                                    autoComplete="off"
                                    maxLength = "11"
                                    placeholder="手机"
                                    onInput = {this.getPhone.bind(this)}
                                />
                            </div>
        
                            <div className="InfoAddress">
                                <label htmlFor="Address">省市区</label>
                                <p
                                    type="text" 
                                    name="Address"
                                    // disabled
                                    onClick={this.openArea.bind(this)} 
                                    onChange = {this.changeArea.bind(this)}
                                    style = {{ 'color'  : !this.state.userAdd ? '#757575' : 'white' }}
                                >
                                {this.state.userAdd || "城市/地区"}
                                </p>
                            </div>
                            <div className="AddressInfo">
                                <label htmlFor="AddressInfo">地址详情</label>
                                <input 
                                    type="text" 
                                    name="AddressInfo"
                                    autoComplete="off" 
                                    placeholder="详细地址"
                                    onInput = {this.getAddress}
                                />
                            </div>
        
                        </form>
                    </div>
                </div>
            </main>


            {/**确定按钮 */}
            <footer  className={this.state.liked ? 'unInfoFooter' : 'InfoFooter'}>
                <div className="InfoB" onClick={this.state.liked ? this.getNo.bind(this) : this.getUserInfo.bind(this)}>
                    <span>确认</span>
                </div>
            </footer>


            <div className="addlistArea_fixbox" style={{'display':this.state.ifshowArea==true?'block':'none'}} ref="Area">
            {/* <div className="addlistArea_fixbox"  ref="Area"> */}
                <div className="addlistArea_boxbg">
                    <div className="addlistArea_box">


                        <div>
                            <ul className="addlistArea_navbar">
                                <li onClick={this.changeArea.bind(this)} data-num="0" >
                                    {this.state.itemNameP||"省份"}
                                </li>
                                <li onClick={this.changeArea.bind(this)} data-num="1" style={{'display':this.state.pAreaIf==true?'block':'none'}}>
                                    {this.state.itemNameC||"城市"}
                                    <p className="ling_bottom"></p>
                                </li>
                                <li onClick={this.changeArea.bind(this)} data-num="2" style={{'display':this.state.pAreaIf==true?'block':'none'}}>
                                    {this.state.itemNameA||"区县"}
                                    <p className="ling_bottom"></p>
                                </li>
                            </ul>
                            <p className="addlistArea_navbar_conbtn" onClick={this.controlAdd.bind(this)} style={{color : `red`}}>确定</p>
                        </div>

                        <div className="line_insert"></div>

                            <ul className="addlistArea" style={{'display':this.state.provinceIf==true?'block':'none'}}>
                                {
                                    this.state.pArea.map(function(item,index){
                                        return(
                                                <li key={index} onClick={this.goCity.bind(this,index,item.name)} data-num={index}>{item.name}</li>
                                            )
                                    },this)
                                }
                            </ul>
                            <ul className="addlistArea addlistArea1" style={{'display':this.state.cityIf==true?'block':'none'}}>
                                {
                                    this.state.cArea.map(function(item,index){
                                        return(
                                                <li key={index} onClick={this.goArea.bind(this,index,item.name)} data-num={index}>{item.name}</li>
                                            )
                                    },this)
                                }
                            </ul>
                            <ul className="addlistArea addlistArea2" style={{'display':this.state.areaIf==true?'block':'none'}}>
                                {
                                    this.state.aArea.map(function(item,index){
                                        return(
                                                <li key={index} onClick={this.goControl.bind(this,index,item)} data-num={index}>{item}</li>
                                            )
                                    },this)
                                }
                            </ul>

                    </div>
                </div>
                
            </div>
            </div>

            
            
            </div>
        )
    }
}
export default UserInfo